const interval = 5;
export const fs20240817 = () => {
  const [index, setIndex] = useState(0);

  // document.getElementsByTagName("body")[0].classList.toggle("overflow-hidden");

  React.useEffect(() => {
    const id = setInterval(() => {
      setIndex((prev) => {
        if (prev === data.length - 1) {
          clearInterval(id);
          return prev;
        }
        return prev + 1;
      });
    }, interval * 1000);
  }, [index]);

  return (
    <div className="w-screen h-screen p-2 font-tegaki bg-gray-200">
      {book()}
    </div>
  );
};

